<template>
  <div class="layout">
    <aside class="aside">
      <div class="menu" @click="$router.push('/')"><span class="iconfont icon-shouye"></span></div>
      <ul class="menus">
        <li
          class="menu"
          :title="menuItem.title"
          v-for="menuItem in menuList"
          :key="menuItem.path"
          :class="{ 'is-active': menuItem.active }"
          @click="$router.push(menuItem.path)"
        >
          <span :class="menuItem.icon"></span>
        </li>
      </ul>
      <div class="menu"><span class="iconfont icon-shezhi"></span></div>
    </aside>
    <main class="main">
      <router-view></router-view>
    </main>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        menuActive: false,
        menuList: [
          { title: 'pro1', path: '/pro/pro1', icon: 'iconfont icon-ziyuan3', active: false },
          { title: 'pro2', path: '/pro/pro2', icon: 'iconfont icon-2', active: false }
        ]
      };
    },
    computed: {
      currentRoute() {
        return this.$route.path;
      }
    },
    watch: {
      // currentRoute(val) {
      //   console.log('监听了');
      //   this.setActiveMenu(val);
      // },
      currentRoute: {
        handler(val) {
          this.setActiveMenu(val);
        },
        immediate: true
      }
    },
    methods: {
      setActiveMenu(path) {
        this.menuList.forEach(item => {
          if (item.path === path) {
            item.active = true;
          } else {
            item.active = false;
          }
        });
      }
    }
  };
</script>
