import scssfile from './pop.scss';
export function pop(title = '复制成功') {
  var isEle = document.querySelector('#tipBox');
  let newDiv = '';
  if (isEle) {
    // 存在dom节点
    return;
  } else {
    // 无
    newDiv = document.createElement('div');
    newDiv.setAttribute('id', 'tipBox');
    newDiv.innerHTML = `<span class="tiptext"><span class="iconfont icon-code-line" title="显示代码"></span>${title}</span>`;
    document.body.appendChild(newDiv);
  }

  setTimeout(() => {
    document.body.removeChild(newDiv);
  }, 1000);
}
